.container{
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 100px;
    background: #fff;
    padding-bottom: 30px;
    width: 600px;
    height: 630px;
    
}
.header
{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
}
.text{
    color: #06077b;
    font-size: 48px;
    font-weight: 700;
}
.underline{
    width: 71px;
    height: 6px;
    background: #06077b;
    border-radius: 9px;
}
.inputs{
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.input{
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 80px;
    background: #cec6c6;
    border-radius: 6px;
}
.input img{
margin: 0px 30px;
height: 30px;
border-radius: 50%;
}
.input input{
    height: 50px;
    width: 350px;
    background: transparent;
    border: none;
    outline: none;
    color: #9b9191;
    font-size: 19px;
}
.forgot-password{
    padding-left: 62px;
    margin-top: 27px;
    color: #9b9191;
    font-size: 18px;
}
.forgot-password span{
    color: rgb(50, 2, 50);
    cursor: pointer;    
}
.submit-container{
    display: flex;
    gap: 30px;
    justify-content: center;
    margin: 60px auto;
    


}
.submit{
    display: flex; 
    
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #fff;
    background: #06077b;
    border-radius: 10px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;
}
.gray{
    background: rgb(237, 225, 225);
    color: gray;
}

